.header{
    width: 100%;
    height: 90vh;
    position: relative;
}

.header-img-container{
    clip-path: circle(33% at 49% 100%);
    position: absolute;
    bottom: 0%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 100%;
    background-color: red;
}
.header-img{
    position: absolute;
    bottom: -27%;
    left: 48.5%;
    margin: auto;
    transform: translateX(-50%);
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.header-title{
    text-transform: uppercase;
    font-weight: lighter;
    font-size: 2.5rem;
    font-family: serif;
}
.header-subtitle{
    margin-top: 0.5rem;
    font-size: 1rem;
    font-weight: 900;
}